Header {
  height: 100vh;
  padding-top: 2rem;
  overflow: hidden;
}
.header_container {
  text-align: center;
  height: 100%;
  position: relative;
}
.CTA {
  margin-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}
.header_social {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
}
.header_social::after {
  content: "";
  width: 2px;
  height: 2rem;
  border-radius: 50%;
  background-color: var(--color-primary);
}
.me {
  background-image: linear-gradient(var(--color-primary), transparent);
  overflow: hidden;
  padding: 1rem 1.5rem 1rem 1.5rem;
  width: 22rem;
  height: fit-content;
  position: absolute;
  left: calc(50% - 11rem);
  margin-top: 2rem;
  border-radius: 12rem 12rem 0 0;
}
.scroll_down {
  position: absolute;
  right: -2.3rem;
  bottom: 5rem;
  transform: rotate(90deg);
  font-weight: 500;
  font-size: 0.9rem;
  color: var(--color-primary);
  cursor: pointer;
}
@media screen and (max-width: 1024px) {
  Header {
    height: 100vh;
  }
}
@media (max-width: 690px) {
  .header_social,
  .scroll_down {
    display: none;
  }
  .header_container h1 {
    /* text-align: center; */
    font-size: 20px;
    letter-spacing: 15px;
    margin: 15px auto;
  }
}
